import React from "react";
import "./TabsEncapsulator.scss";
import { useState } from "react";
import * as classnames from "classnames";

export default ({ tabs }) => {
  const [myTabs, setMyTabs] = useState(
    tabs.map((t) => ({ active: t.active || false, ...t }))
  );

  const TabClickEvent = ({ tabId }) => {
    setMyTabs(
      myTabs.map((t) =>
        t.title !== tabId ? { ...t, active: false } : { ...t, active: true }
      )
    );
  };

  return (
    <div className="TabsEncapsulator_Container">
      <div className="TabsEncapsulator_TabsContainer">
        {myTabs.map((t) => (
          <TabElement
            key={t.title}
            title={t.title}
            isActive={t.active}
            tabClickEvent={TabClickEvent}
          />
        ))}
      </div>
      {myTabs.map((t) => (
        <div
          key={t.title}
          className={classnames({
            TabsEncapsulator_ContentWrapper: true,
            "TabsEncapsulator_ContentWrapper--Active": t.active,
          })}
        >
          {t.content ? t.content : <h4>NO CONTENT: {` ${t.title}`}</h4>}
        </div>
      ))}
    </div>
  );
};

// export default TabsEncapsulator;

export const TabElement = ({ title, isActive, tabClickEvent }) => {
  return (
    <div
      className={classnames({
        TabElement_Container: true,
        "TabElement_Container--Active": isActive,
      })}
      onClick={() => {
        tabClickEvent({ tabId: title });
      }}
    >
      {title}
    </div>
  );
};
