import React from "react";
import "./TrainingList.scss";

const TrainingList = ({ provider, year, subject, website }) => {
  return (
    <div className="TrainingListContainer">
      <div className="Training_Headers_Container">
        <div className="Training_Header_Container">
          <div className="Training_Header_1">Provider:</div>
          <div className="Training_Header_1">Year:</div>
          <div className="Training_Header_1">Subject:</div>
          <div className="Training_Header_1">Website:</div>
        </div>

        <div className="Training_Header_Container">
          <div className="Training_Header_2">{provider}</div>
          <div className="Training_Header_2">{year}</div>
          <div className="Training_Header_2">{subject}</div>
          <div className="Training_Header_2">{website}</div>
        </div>
      </div>
    </div>
  );
};

export default TrainingList;
