import React from "react";
import "./ProjectsScreen.scss";
import PageHeader from "../Components/PageHeader";
import ProjectDisplay, { constants } from "../Components/ProjectDisplay";
import { paths } from "../appConfig";
import TabsEncapsulator from "../Components/Tab/TabsEncapsulator";

const MobileAndWebProjects = () => (
  <>
    <div className="ProjectsContainer">
      <ProjectDisplay
        projectType={constants.MOBILE}
        orientation={constants.PORTRAIT}
        projectName="Insights Discovery"
        numOfImages={42}
      ></ProjectDisplay>

      <ProjectDisplay
        projectType={constants.MOBILE}
        orientation={constants.PORTRAIT}
        projectName="Insights Marketing"
        numOfImages={30}
      ></ProjectDisplay>
    </div>

    {/* <div className="Divider"></div> */}

    <div className="ProjectsContainer">
      <ProjectDisplay
        projectType={constants.WEB}
        orientation={constants.LANDSCAPE}
        projectName="Insights Team Wheel"
        numOfImages={8}
      ></ProjectDisplay>
    </div>
  </>
);

const Workstation = () => {
  return (
    <div className="WorkstationContainer">
      <img
        src={encodeURI("./Projects/Workstation/01.JPG")}
        className="WorkstationBackground"
        alt="wrks"
      ></img>

      <div className="SpecList">
        <div className="SpecList_ItemsContainer">
          <div className="SpecList_Item_1">MODEL:</div>
          <div className="SpecList_Item_1">OS:</div>
          <div className="SpecList_Item_1">AGE:</div>
          <div className="SpecList_Item_1">CPU:</div>
          <div className="SpecList_Item_1">HDD:</div>
          <div className="SpecList_Item_1">RAM:</div>
          <div className="SpecList_Item_1">EGPU:</div>
        </div>

        <div className="SpecList_ItemsContainer">
          <div className="SpecList_Item_2">MSI GT70</div>
          <div className="SpecList_Item_2">WIN 10</div>
          <div className="SpecList_Item_2">10 Years</div>
          <div className="SpecList_Item_2">i7-3610QM</div>
          <div className="SpecList_Item_2">
            RAID0: 2x512GB SSD | 1x500GB SSD
          </div>
          <div className="SpecList_Item_2">16GB</div>
          <div className="SpecList_Item_2">NVIDIA GTX 1660S</div>
        </div>
      </div>

      <div className="Divider"></div>

      <div className="SpecList">
        <div className="SpecList_ItemsContainer">
          <div className="SpecList_Item_1">MODEL:</div>
          <div className="SpecList_Item_1">OS:</div>
          <div className="SpecList_Item_1">AGE:</div>
          <div className="SpecList_Item_1">CPU:</div>
          <div className="SpecList_Item_1">HDD:</div>
          <div className="SpecList_Item_1">RAM:</div>
        </div>

        <div className="SpecList_ItemsContainer">
          <div className="SpecList_Item_2">MAC BOOK PRO (2013)</div>
          <div className="SpecList_Item_2">OSX</div>
          <div className="SpecList_Item_2">7 Years</div>
          <div className="SpecList_Item_2">i7</div>
          <div className="SpecList_Item_2">250GB SSD</div>
          <div className="SpecList_Item_2">8GB</div>
        </div>
      </div>
    </div>
  );
};

const Nas = () => {
  const pics = ["01.jpg", "02.jpg"]; //, "03.jpg"];
  return (
    <div className="NasContainer">
      <div className="NasPicsContainer">
        {pics.map((p) => (
          <img key={p} alt={p} src={encodeURI(`./Projects/Nas/${p}`)}></img>
        ))}
      </div>
      <div className="SpecList">
        <div className="SpecList_ItemsContainer">
          <div className="SpecList_Item_1">HARDWARE:</div>
          <div className="SpecList_Item_1">OS:</div>
          <div className="SpecList_Item_1">RAM:</div>
          <div className="SpecList_Item_1">NAS SOFT:</div>
          <div className="SpecList_Item_1">HDD SETUP:</div>
          <div className="SpecList_Item_1">USES:</div>
        </div>

        <div className="SpecList_ItemsContainer">
          <div className="SpecList_Item_2">SBC Raspberry Pi 4</div>
          <div className="SpecList_Item_2">Raspbian</div>
          <div className="SpecList_Item_2">4GB</div>
          <div className="SpecList_Item_2">{`Open Media Vault (OMV)`}</div>
          <div className="SpecList_Item_2">{`RAID1: [2x2TB, 2x500GB]`}</div>
          <div className="SpecList_Item_2">
            <ul>
              <li>{`Data Storage | Backup | Sharing`}</li>
              <li>{`Web Apps Testing & Serving Using Docker Containers`}</li>
            </ul>
            {/* {`Systems & Hardware Monitoring`} */}
          </div>
        </div>
      </div>
    </div>
  );
};

const Hass = () => {
  const pics = ["01.jpg"];
  return (
    <div className="HassContainer">
      <div className="HassPicsContainer">
        {pics.map((p) => (
          <img key={p} alt={p} src={encodeURI(`./Projects/Hass/${p}`)}></img>
        ))}
      </div>

      <div className="SpecList">
        <div className="SpecList_ItemsContainer">
          <div className="SpecList_Item_1">HARDWARE:</div>
          <div className="SpecList_Item_1">OS:</div>
          <div className="SpecList_Item_1">RAM:</div>
          {/* <div className="SpecList_Item_1">CONTROLLER:</div> */}
          <div className="SpecList_Item_1">USES:</div>
        </div>

        <div className="SpecList_ItemsContainer">
          <div className="SpecList_Item_2">SBC Raspberry Pi 3</div>
          <div className="SpecList_Item_2">HASS IO</div>
          <div className="SpecList_Item_2">1GB</div>
          {/* <div className="SpecList_Item_2">Old Smartphone</div> */}

          <div className="SpecList_Item_2">
            <ul>
              <li>{`Control Smart Switches`}</li>
              <li>{`Automate Events`}</li>
              <li>{`Switch Off computers using SSH (public,private key authentication)`}</li>
              <li>{`Monitor Appliances Status (diplayed using NAS screen)`}</li>
            </ul>
            {/* {`Systems & Hardware Monitoring`} */}
          </div>
        </div>
      </div>
    </div>
  );
};

const ProjectsScreen = () => {
  const tabsData = [
    { title: "MOBILE & WEB", active: true, content: MobileAndWebProjects() },
    { title: "WORKSTATION", active: false, content: Workstation() },
    { title: "NAS", active: false, content: Nas() },
    { title: "HASS IO", active: false, content: Hass() },
    // { title: "MASTERS" },
  ];

  return (
    <>
      <PageHeader header="PROJECTS" iconName={paths.projects.icon_name} />
      <TabsEncapsulator tabs={tabsData} />
    </>
  );
};

export default ProjectsScreen;
